<template lang="html">
    <main-layout :settings-breadcrumbs="settingsBreadcrumbs">
        <template #content>
            <profile-form-layout
                :show-modal="showModal"
                @close-modal="showModal=false"
                @confirm="confirm"
            >
                <template #headerIcon>
                    <phone />
                </template>
                <template #form>
                    <div class="profile-info__form">
                        <div class="profile-info__form-group">
                            <div class="profile-info__form-row">
                                <div class="profile-info__col100">
                                    <phone-number
                                        @phoneValidated="validatePhone"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </template>
            </profile-form-layout>
        </template>
    </main-layout>
</template>

<script>
import { eventBus } from '@/helpers/event-bus';
import { validationMixin } from 'vuelidate'
import formValidationMixin from '@/mixins/formValidationMixin'
import { validationMessage } from 'vuelidate-messages'
import { validationRules } from '@/validation/phone/Rules.js'
import { formMessages } from '@/validation/phone/Messages.js'

import PhoneNumber from '@/components/common/form-elements/phone/PhoneNumber';
import MainLayout from '@/layouts/MainLayout';
import ProfileFormLayout from '@/layouts/profile/ProfileFormLayout';
import Phone from 'mdi-vue/Phone'

import { createNamespacedHelpers, mapState } from 'vuex';
import {
    ACTION_CLIENT_UPDATE_PHONE
} from '@/store/modules/client/action-types';
import {
    UPDATE_CLIENT_PHONE_DETAILS
} from '@/store/modules/client/mutation-types';
import { mapPhoneFields } from '@/store/modules/phone'

const {
    mapActions: mapClientActions,
    mapMutations: mapClientMutations
} = createNamespacedHelpers('client');

export default {
    components: {
        PhoneNumber,
        MainLayout,
        ProfileFormLayout,
        Phone
    },
    validations: {
        ...validationRules
    },
    mixins: [
        validationMixin,
        formValidationMixin
    ],
    data() {
        return {
            settingsBreadcrumbs: {
                title: 'Phone',
                steps: [
                    { url: '/', title: 'Home' },
                    { url: '/cp/account', title: 'Account' },
                    { url: '/cp/profile/phone', title: 'Phone' }
                ],
                additionalClass: 'bc-wrapper bc-wrapper_user-account bc-wrapper--setting bc-right-bottom bc-horizontal'
            },
            showModal: false,
            phoneValidationStatus: false
        }
    },
    computed: {
        ...mapState('phone', {
            started: 'started',
            verified: 'verified'
        }),
        ...mapPhoneFields([
            'form_data.country',
            'form_data.phone_number'
        ])
    },
    mounted() {
        this.$store.commit('phone/SET_DEFAULT')
        eventBus.$on('validate', () => {
            this.check()
        })
    },
    methods: {
        ...mapClientActions({
            updatePhone: ACTION_CLIENT_UPDATE_PHONE
        }),
        ...mapClientMutations([
            UPDATE_CLIENT_PHONE_DETAILS
        ]),
        validationMessage: validationMessage(formMessages),
        validatePhone(value) {
            this.phoneValidationStatus = value
        },
        confirm() {
            try {
                const response = this.updatePhone({
                    phone_code: this.country.value,
                    phone: this.phone_number
                })
                if (response) {
                    eventBus.$emit('showSnackBar', 'Your phone has been changed', 'success');
                    this[UPDATE_CLIENT_PHONE_DETAILS]({
                        phone: this.phone_number,
                        code: this.country.value
                    })
                    this.$router.push({ name: 'profile' })
                }
            } catch (err) {
                console.log(err)
            }
        },
        check() {
            if (!this.phoneValidationStatus) {
                this.validate()
                return
            }
            this.showModal = true
        },
        submit() {
            this.showModal = true
        }
    }
}
</script>

<style lang="scss">
    @media screen and (max-width: 576px){
        .phone__row {
            .form-select {
                width: 30%;
                min-width: 30%;
            }
            .form-input__fieldset {
                width: 70%;
            }
            &-verify {
                margin-top: 24px;
            }
            .phone-verification__btn {
                margin-bottom: 0;
            }
        }
    }
</style>
